import React, { useEffect, useRef } from "react";
import gsap from "gsap";
import tw from "twin.macro";
import { navigate } from "gatsby";
import AniLink from "gatsby-plugin-transition-link/AniLink";
import { TransitionPortal } from "gatsby-plugin-transition-link";
import Layout from "../components/layout";
import Seo from "../components/seo";
import MeetUp from "../components/Sections/meetup-section";
import FollowMe from "../components/Sections/social-media-section";
import { MaxContainer } from "../components/share-ui";

const IndexPage = ({ location }) => {
  const layoutContents = useRef();
  const transitionCover = useRef();
  useEffect(()=>{
    navigate("/coupon/")
  },[])
  const verticalAnimation = ({ length }, direction) => {
    const directionTo = direction === "left" ? "-100%" : "100%";
    const directionFrom = direction === "left" ? "100%" : "-100%";

    // convert ms to s for gsap
    const seconds = length;

    return gsap
      .timeline()
      .set(transitionCover.current, { x: directionFrom })
      .to(transitionCover.current, {
        x: "0%",
        ease: "power1.easeInOut",
        duration: seconds / 2,
      })
      .set(layoutContents.current, { opacity: 0 })
      .to(transitionCover.current, {
        x: directionTo,
        ease: "power1.easeIn",
        duration: seconds / 2,
      });
  };

  return (
    <Layout location={location}>
      <Seo title="Home" description="Welcome to Corgi Japser's website" />
      {/*<section ref={layoutContents}>*/}
      {/*  <div tw="py-10 text-center">*/}
      {/*    <MaxContainer>*/}
      {/*      <h3 tw="text-xl font-bold">Learn more about Jasper!</h3>*/}
      {/*      <div tw="my-4 bg-green-400 px-3 py-10 rounded-tl-md">What is on Jasper's menu?</div>*/}
      {/*      <div tw="my-4 bg-green-100 px-3 py-10">What Jasper owns?</div>*/}
      {/*      <div tw="my-4 bg-green-400 px-3 py-10">Where Jasper goes trail?</div>*/}
      {/*      <div tw="my-4 bg-green-100 px-3 py-10 rounded-br-md">*/}
      {/*        <AniLink*/}
      {/*          to="/coupon"*/}
      {/*          exit={{*/}
      {/*            length: 1.2,*/}
      {/*            trigger: ({ exit }) => verticalAnimation(exit, "left"),*/}
      {/*          }}*/}
      {/*          entry={{ delay: 0.5, length: 1, state: { layoutTheme: "dark" } }}*/}
      {/*        >*/}
      {/*          Coupon*/}
      {/*        </AniLink>*/}
      {/*      </div>*/}
      {/*    </MaxContainer>*/}
      {/*  </div>*/}
      {/*  <MeetUp />*/}
      {/*  <FollowMe />*/}
      {/*</section>*/}
      {/*<TransitionPortal>*/}
      {/*  <div*/}
      {/*    ref={transitionCover}*/}
      {/*    style={{*/}
      {/*      position: "fixed",*/}
      {/*      background: "#40916c",*/}
      {/*      top: 0,*/}
      {/*      left: 0,*/}
      {/*      width: "100vw",*/}
      {/*      height: "100vh",*/}
      {/*      transform: "translateX(100%)",*/}
      {/*    }}*/}
      {/*  />*/}
      {/*</TransitionPortal>*/}
    </Layout>
  );
};
export default IndexPage;
